// ----------------------------------------------------------------------------
// Rules
// ----------------------------------------------------------------------------

// Icon
.md-icon {

  // SVG defaults
  svg {
    display: block;
    width: px2rem(24px);
    height: px2rem(24px);
    fill: currentcolor;
  }
}

// sphinx-immaterial: change display for inline icons
.si-icon-inline::before {
  display: inline-flex;
  width: px2em(18px);
  height: px2em(18px);
  vertical-align: text-top;
  content: "";
  background-color: var(--md-default-fg-color);
  mask-repeat: no-repeat;
  mask-position: center;
}
