@charset "UTF-8";
html {
  box-sizing: border-box;
  text-size-adjust: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
@media (prefers-reduced-motion) {
  *,
  *::before,
  *::after {
    transition: none !important;
  }
}

body {
  margin: 0;
}

a,
button,
label,
input {
  -webkit-tap-highlight-color: transparent;
}

a {
  color: inherit;
  text-decoration: none;
}

hr {
  box-sizing: content-box;
  display: block;
  height: 0.05rem;
  padding: 0;
  overflow: visible;
  border: 0;
}

small {
  font-size: 80%;
}

sub,
sup {
  line-height: 1em;
}

img {
  border-style: none;
}

table {
  border-spacing: 0;
  border-collapse: separate;
}

td,
th {
  font-weight: 400;
  vertical-align: top;
}

button {
  padding: 0;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  background: transparent;
  border: 0;
}

input {
  border: 0;
  outline: none;
}

:root {
  --md-primary-fg-color: hsla(231, 48%, 48%, 1);
  --md-primary-fg-color--light: hsla(231, 44%, 56%, 1);
  --md-primary-fg-color--dark: hsla(232, 54%, 41%, 1);
  --md-primary-bg-color: hsla(0, 0%, 100%, 1);
  --md-primary-bg-color--light: hsla(0, 0%, 100%, 0.7);
  --md-accent-fg-color: hsla(231, 99%, 66%, 1);
  --md-accent-fg-color--transparent: hsla(231, 99%, 66%, 0.1);
  --md-accent-bg-color: hsla(0, 0%, 100%, 1);
  --md-accent-bg-color--light: hsla(0, 0%, 100%, 0.7);
}

[data-md-color-scheme=default] {
  color-scheme: light;
}
[data-md-color-scheme=default] img[src$="#only-dark"],
[data-md-color-scheme=default] img[src$="#gh-dark-mode-only"] {
  display: none;
}

:root, [data-md-color-scheme=default] {
  --md-hue: 225deg;
  --md-default-fg-color: hsla(0, 0%, 0%, 0.87);
  --md-default-fg-color--light: hsla(0, 0%, 0%, 0.54);
  --md-default-fg-color--lighter: hsla(0, 0%, 0%, 0.32);
  --md-default-fg-color--lightest: hsla(0, 0%, 0%, 0.07);
  --md-default-bg-color: hsla(0, 0%, 100%, 1);
  --md-default-bg-color--light: hsla(0, 0%, 100%, 0.7);
  --md-default-bg-color--lighter: hsla(0, 0%, 100%, 0.3);
  --md-default-bg-color--lightest: hsla(0, 0%, 100%, 0.12);
  --md-code-fg-color: hsla(200, 18%, 26%, 1);
  --md-code-bg-color: hsla(200, 0%, 96%, 1);
  --md-code-hl-color: hsla(218, 100%, 63%, 1);
  --md-code-hl-color--light: hsla(218, 100%, 63%, 0.1);
  --md-code-hl-number-color: hsla(0, 67%, 50%, 1);
  --md-code-hl-special-color: hsla(340, 83%, 47%, 1);
  --md-code-hl-function-color: hsla(291, 45%, 50%, 1);
  --md-code-hl-constant-color: hsla(250, 63%, 60%, 1);
  --md-code-hl-keyword-color: hsla(219, 54%, 51%, 1);
  --md-code-hl-string-color: hsla(150, 63%, 30%, 1);
  --md-code-hl-name-color: var(--md-code-fg-color);
  --md-code-hl-operator-color: var(--md-default-fg-color--light);
  --md-code-hl-punctuation-color: var(--md-default-fg-color--light);
  --md-code-hl-comment-color: var(--md-default-fg-color--light);
  --md-code-hl-generic-color: var(--md-default-fg-color--light);
  --md-code-hl-variable-color: var(--md-default-fg-color--light);
  --md-typeset-color: var(--md-default-fg-color);
  --md-typeset-a-color: var(--md-primary-fg-color);
  --md-typeset-del-color: hsla(6, 90%, 60%, 0.15);
  --md-typeset-ins-color: hsla(150, 90%, 44%, 0.15);
  --md-typeset-kbd-color: hsla(0, 0%, 98%, 1);
  --md-typeset-kbd-accent-color: hsla(0, 100%, 100%, 1);
  --md-typeset-kbd-border-color: hsla(0, 0%, 72%, 1);
  --md-typeset-mark-color: hsla(60, 100%, 50%, 0.5);
  --md-typeset-table-color: hsla(0, 0%, 0%, 0.12);
  --md-typeset-table-color--light: hsla(0, 0%, 0%, 0.035);
  --md-admonition-fg-color: var(--md-default-fg-color);
  --md-admonition-bg-color: var(--md-default-bg-color);
  --md-warning-fg-color: hsla(0, 0%, 0%, 0.87);
  --md-warning-bg-color: hsla(60, 100%, 80%, 1);
  --md-footer-fg-color: hsla(0, 0%, 100%, 1);
  --md-footer-fg-color--light: hsla(0, 0%, 100%, 0.7);
  --md-footer-fg-color--lighter: hsla(0, 0%, 100%, 0.45);
  --md-footer-bg-color: hsla(0, 0%, 0%, 0.87);
  --md-footer-bg-color--dark: hsla(0, 0%, 0%, 0.32);
  --md-shadow-z1:
    0 0.2rem 0.5rem hsla(0, 0%, 0%, 0.05),
    0 0 0.05rem hsla(0, 0%, 0%, 0.1);
  --md-shadow-z2:
    0 0.2rem 0.5rem hsla(0, 0%, 0%, 0.1),
    0 0 0.05rem hsla(0, 0%, 0%, 0.25);
  --md-shadow-z3:
    0 0.2rem 0.5rem hsla(0, 0%, 0%, 0.2),
    0 0 0.05rem hsla(0, 0%, 0%, 0.35);
}

.md-icon svg {
  display: block;
  width: 1.2rem;
  height: 1.2rem;
  fill: currentcolor;
}

.si-icon-inline::before {
  display: inline-flex;
  width: 1.125em;
  height: 1.125em;
  vertical-align: text-top;
  content: "";
  background-color: var(--md-default-fg-color);
  mask-repeat: no-repeat;
  mask-position: center;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --md-text-font-family:
    var(--md-text-font, _),
    -apple-system, BlinkMacSystemFont, Helvetica, Arial, sans-serif;
  --md-code-font-family:
    var(--md-code-font, _),
    SFMono-Regular, Consolas, Menlo, monospace;
}

body,
input,
aside {
  font-family: var(--md-text-font-family);
  font-feature-settings: "kern", "liga";
  color: var(--md-typeset-color);
}

code,
pre,
kbd {
  font-family: var(--md-code-font-family);
  font-feature-settings: "kern";
}

:root {
  --md-typeset-table-sort-icon: svg-load("material/sort.svg");
  --md-typeset-table-sort-icon--asc: svg-load("material/sort-ascending.svg");
  --md-typeset-table-sort-icon--desc: svg-load("material/sort-descending.svg");
}

.md-typeset {
  font-size: 0.8rem;
  line-height: 1.6;
  overflow-wrap: break-word;
  color-adjust: exact;
}
@media print {
  .md-typeset {
    font-size: 0.68rem;
  }
}
.md-typeset ul,
.md-typeset ol,
.md-typeset dl,
.md-typeset figure,
.md-typeset blockquote,
.md-typeset pre {
  margin-block: 1em;
}
.md-typeset h1 {
  margin: 0 0 1.25em;
  font-size: 2em;
  font-weight: 300;
  line-height: 1.3;
  color: var(--md-default-fg-color--light);
  letter-spacing: -0.01em;
}
.md-typeset h2 {
  margin: 1.6em 0 0.64em;
  font-size: 1.5625em;
  font-weight: 300;
  line-height: 1.4;
  letter-spacing: -0.01em;
}
.md-typeset h3 {
  margin: 1.6em 0 0.8em;
  font-size: 1.25em;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: -0.01em;
}
.md-typeset h2 + h3 {
  margin-top: 0.8em;
}
.md-typeset h4 {
  margin: 1em 0;
  font-weight: 700;
  letter-spacing: -0.01em;
}
.md-typeset h5,
.md-typeset h6 {
  margin: 1.25em 0;
  font-size: 0.8em;
  font-weight: 700;
  color: var(--md-default-fg-color--light);
  letter-spacing: -0.01em;
}
.md-typeset h5 {
  text-transform: uppercase;
}
.md-typeset h5 code {
  text-transform: none;
}
.md-typeset hr {
  display: flow-root;
  margin: 1.5em 0;
  border-bottom: 0.05rem solid var(--md-default-fg-color--lightest);
}
.md-typeset a {
  color: var(--md-typeset-a-color);
  word-break: break-word;
}
.md-typeset a, .md-typeset a::before {
  transition: color 125ms;
}
.md-typeset a:is(:focus, :hover) {
  color: var(--md-accent-fg-color);
}
.md-typeset a:is(:focus, :hover) code {
  background-color: var(--md-accent-fg-color--transparent);
}
.md-typeset a code {
  color: currentcolor;
  transition: background-color 125ms;
}
.md-typeset a.focus-visible {
  outline-color: var(--md-accent-fg-color);
  outline-offset: 0.2rem;
}
.md-typeset code,
.md-typeset pre,
.md-typeset kbd {
  font-variant-ligatures: none;
  color: var(--md-code-fg-color);
  direction: ltr;
}
@media print {
  .md-typeset code,
  .md-typeset pre,
  .md-typeset kbd {
    white-space: pre-wrap;
  }
}
.md-typeset code {
  padding: 0 0.2941176471em;
  font-size: 0.85em;
  word-break: break-word;
  background-color: var(--md-code-bg-color);
  border-radius: 0.1rem;
  box-decoration-break: clone;
}
.md-typeset code:not(.focus-visible) {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}
.md-typeset pre {
  position: relative;
  display: flow-root;
  line-height: 1.4;
}
.md-typeset pre > code {
  display: block;
  padding: 0.7720588235em 1.1764705882em;
  margin: 0;
  overflow: auto;
  word-break: normal;
  touch-action: auto;
  scrollbar-color: var(--md-default-fg-color--lighter) transparent;
  scrollbar-width: thin;
  outline-color: var(--md-accent-fg-color);
  box-shadow: none;
  box-decoration-break: slice;
}
.md-typeset pre > code:hover {
  scrollbar-color: var(--md-accent-fg-color) transparent;
}
.md-typeset pre > code::-webkit-scrollbar {
  width: 0.2rem;
  height: 0.2rem;
}
.md-typeset pre > code::-webkit-scrollbar-thumb {
  background-color: var(--md-default-fg-color--lighter);
}
.md-typeset pre > code::-webkit-scrollbar-thumb:hover {
  background-color: var(--md-accent-fg-color);
}
.md-typeset .code-block-caption + .notranslate pre,
.md-typeset .code-block-caption + .notranslate .highlighttable {
  margin-top: 0;
}
.md-typeset kbd {
  display: inline-block;
  padding: 0 0.6666666667em;
  font-size: 0.75em;
  color: var(--md-default-fg-color);
  word-break: break-word;
  vertical-align: text-top;
  background-color: var(--md-typeset-kbd-color);
  border-radius: 0.1rem;
  box-shadow: 0 0.1rem 0 0.05rem var(--md-typeset-kbd-border-color), 0 0.1rem 0 var(--md-typeset-kbd-border-color), 0 -0.1rem 0.2rem var(--md-typeset-kbd-accent-color) inset;
}
.md-typeset mark {
  color: inherit;
  word-break: break-word;
  background-color: var(--md-typeset-mark-color);
  box-decoration-break: clone;
}
.md-typeset abbr {
  text-decoration: none;
  cursor: help;
  border-bottom: 0.05rem dotted var(--md-default-fg-color--light);
}
.md-typeset small {
  opacity: 0.75;
}
.md-typeset sup,
.md-typeset sub {
  margin-inline-start: 0.078125em;
}
.md-typeset blockquote {
  padding-inline-start: 0.6rem;
  margin-inline: 0;
  color: var(--md-default-fg-color--light);
  border-inline-start: 0.2rem solid var(--md-default-fg-color--lighter);
}
.md-typeset ul {
  list-style-type: disc;
}
.md-typeset ul[type] {
  list-style-type: revert-layer;
}
.md-typeset ul,
.md-typeset ol {
  padding: 0;
  margin-inline-start: 0.625em;
}
.md-typeset ul:not([hidden]),
.md-typeset ol:not([hidden]) {
  display: flow-root;
}
.md-typeset ul ol,
.md-typeset ol ol {
  list-style-type: lower-alpha;
}
.md-typeset ul ol ol,
.md-typeset ol ol ol {
  list-style-type: lower-roman;
}
.md-typeset ul ol ol ol,
.md-typeset ol ol ol ol {
  list-style-type: upper-alpha;
}
.md-typeset ul ol ol ol ol,
.md-typeset ol ol ol ol ol {
  list-style-type: upper-roman;
}
.md-typeset ul[type],
.md-typeset ol[type] {
  list-style-type: revert-layer;
}
.md-typeset ul li,
.md-typeset ol li {
  margin-inline-start: 1.25em;
  margin-bottom: 0.5em;
}
.md-typeset ul li p,
.md-typeset ul li blockquote,
.md-typeset ol li p,
.md-typeset ol li blockquote {
  margin: 0.5em 0;
}
.md-typeset ul li:last-child,
.md-typeset ol li:last-child {
  margin-bottom: 0;
}
.md-typeset ul li :is(ul, ol),
.md-typeset ol li :is(ul, ol) {
  margin-block: 0.5em;
  margin-inline-start: 0.625em;
}
.md-typeset dd {
  margin-block: 1em 1.5em;
  margin-inline-start: 1.875em;
}
.md-typeset img,
.md-typeset svg,
.md-typeset video {
  max-width: 100%;
  height: auto;
}
.md-typeset img[align=left] {
  margin: 1em;
  margin-left: 0;
}
.md-typeset img[align=right] {
  margin: 1em;
  margin-right: 0;
}
.md-typeset img[align]:only-child {
  margin-top: 0;
}
.md-typeset figure {
  display: flow-root;
  width: fit-content;
  max-width: 100%;
  margin: 1em auto;
  text-align: center;
}
.md-typeset figure img {
  display: block;
  margin: 0 auto;
}
.md-typeset figcaption {
  max-width: 24rem;
  margin: 1em auto;
  font-style: italic;
}
.md-typeset iframe {
  max-width: 100%;
}
.md-typeset table.data:not(.plain) {
  display: block;
  width: max-content;
  max-width: 100%;
  overflow: auto;
  font-size: 0.64rem;
  touch-action: auto;
  background-color: var(--md-default-bg-color);
  border: 0.05rem solid var(--md-typeset-table-color);
  border-radius: 0.1rem;
}
@media print {
  .md-typeset table.data:not(.plain) {
    display: table;
  }
}
.md-typeset table.data:not(.plain) + * {
  margin-top: 1.5em;
}
.md-typeset table.data:not(.plain) :is(th, td) > *:first-child {
  margin-top: 0;
}
.md-typeset table.data:not(.plain) :is(th, td) > *:last-child {
  margin-bottom: 0;
}
.md-typeset table.data:not(.plain) :is(th, td):not([align], .align-center, .align-left, .align-right) {
  text-align: left;
}
[dir=rtl] .md-typeset table.data:not(.plain) :is(th, td):not([align], .align-center, .align-left, .align-right) {
  text-align: right;
}
.md-typeset table.data:not(.plain) th {
  min-width: 5rem;
  padding: 0.9375em 1.25em;
  font-weight: 700;
  vertical-align: top;
}
.md-typeset table.data:not(.plain) td {
  padding: 0.9375em 1.25em;
  vertical-align: top;
  border-top: 0.05rem solid var(--md-typeset-table-color);
}
.md-typeset table.data:not(.plain) tbody tr {
  transition: background-color 125ms;
}
.md-typeset table.data:not(.plain) tbody tr:hover {
  background-color: var(--md-typeset-table-color--light);
  box-shadow: 0 0.05rem 0 var(--md-default-bg-color) inset;
}
.md-typeset table.data:not(.plain) a {
  word-break: normal;
}
.md-typeset table th[role=columnheader] {
  cursor: pointer;
}
.md-typeset table th[role=columnheader]::after {
  display: inline-block;
  width: 1.2em;
  height: 1.2em;
  margin-inline-start: 0.5em;
  vertical-align: text-bottom;
  content: "";
  mask-image: var(--md-typeset-table-sort-icon);
  mask-repeat: no-repeat;
  mask-size: contain;
  transition: background-color 125ms;
}
.md-typeset table th[role=columnheader]:hover::after {
  background-color: var(--md-default-fg-color--lighter);
}
.md-typeset table th[role=columnheader][aria-sort=ascending]::after {
  background-color: var(--md-default-fg-color--light);
  mask-image: var(--md-typeset-table-sort-icon--asc);
}
.md-typeset table th[role=columnheader][aria-sort=descending]::after {
  background-color: var(--md-default-fg-color--light);
  mask-image: var(--md-typeset-table-sort-icon--desc);
}
.md-typeset__scrollwrap {
  margin: 1em -0.8rem;
  overflow-x: auto;
  touch-action: auto;
}
.md-typeset__table {
  display: inline-block;
  padding: 0 0.8rem;
  margin-bottom: 0.5em;
}
@media print {
  .md-typeset__table {
    display: block;
  }
}
html .md-typeset__table table {
  display: table;
  width: 100%;
  margin: 0;
  overflow: hidden;
}

@media screen and (max-width: 44.984375em) {
  .md-content__inner > pre {
    margin: 1em -0.8rem;
  }
  .md-content__inner > pre code {
    border-radius: 0;
  }
}
.md-typeset .md-author {
  position: relative;
  display: block;
  flex-shrink: 0;
  width: 1.6rem;
  height: 1.6rem;
  overflow: hidden;
  border-radius: 100%;
  transition: color 125ms, transform 125ms;
}
.md-typeset .md-author img {
  display: block;
}
.md-typeset .md-author--more {
  font-size: 0.6rem;
  font-weight: 700;
  line-height: 1.6rem;
  color: var(--md-default-fg-color--lighter);
  text-align: center;
  background: var(--md-default-fg-color--lightest);
}
.md-typeset .md-author--long {
  width: 2.4rem;
  height: 2.4rem;
}
.md-typeset a.md-author {
  transform: scale(1);
}
.md-typeset a.md-author img {
  filter: grayscale(100%) opacity(75%);
  border-radius: 100%;
  transition: filter 125ms;
}
.md-typeset a.md-author:is(:focus, :hover) {
  z-index: 1;
  transform: scale(1.1);
}
.md-typeset a.md-author:is(:focus, :hover) img {
  filter: grayscale(0%);
}

.md-banner {
  overflow: auto;
  color: var(--md-footer-fg-color);
  background-color: var(--md-footer-bg-color);
}
@media print {
  .md-banner {
    display: none;
  }
}
.md-banner--warning {
  color: var(--md-warning-fg-color);
  background-color: var(--md-warning-bg-color);
}
.md-banner__inner {
  padding: 0 0.8rem;
  margin: 0.6rem auto;
  font-size: 0.7rem;
}
.md-banner__button {
  float: inline-end;
  color: inherit;
  cursor: pointer;
  transition: opacity 250ms;
}
.no-js .md-banner__button {
  display: none;
}
.md-banner__button:hover {
  opacity: 0.7;
}

html {
  height: 100%;
  overflow-x: hidden;
  font-size: 125%;
}
@media screen and (min-width: 100em) {
  html {
    font-size: 137.5%;
  }
}
@media screen and (min-width: 125em) {
  html {
    font-size: 150%;
  }
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  font-size: 0.5rem;
  background-color: var(--md-default-bg-color);
}
@media print {
  body {
    display: block;
  }
}
@media screen and (max-width: 59.984375em) {
  body[data-md-scrolllock] {
    position: fixed;
  }
}

.md-grid {
  max-width: 61rem;
  margin-inline: auto;
}

.md-container {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}
@media print {
  .md-container {
    display: block;
  }
}

.md-main {
  flex-grow: 1;
}
.md-main__inner {
  display: flex;
  height: 100%;
  margin-top: 1.5rem;
}

.md-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}

.md-toggle {
  display: none;
}

.md-option {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}
.md-option:checked + label:not([hidden]) {
  display: block;
}
.md-option.focus-visible + label {
  outline-style: auto;
  outline-color: var(--md-accent-fg-color);
}

.md-skip {
  position: fixed;
  z-index: -1;
  padding: 0.3rem 0.5rem;
  margin: 0.5rem;
  font-size: 0.64rem;
  color: var(--md-default-bg-color);
  background-color: var(--md-default-fg-color);
  border-radius: 0.1rem;
  outline-color: var(--md-accent-fg-color);
  opacity: 0;
  transform: translateY(0.4rem);
}
.md-skip:focus {
  z-index: 10;
  opacity: 1;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), opacity 175ms 75ms;
  transform: translateY(0);
}

@page {
  margin: 25mm;
}
:root {
